body {
  font-family: monospace;
  font-size: 14px;
  line-height: 1.42857143;
  color: #333;
  background: #eeeeee;
}

#spinner {
  height: 100%;
  width: 100%;
  z-index: 2;
  background: #eeeeee;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

body p.modes {
  text-align: center;
  margin: 2px 0 0;
  font-size: 12px;
}

body p.modes a {
  font-family: monospace;
  text-decoration: none;
}

body .container {
  position: relative;
  left: 0;
  right: 0;
  max-width: 100vw;
  max-height: 100vh;
  height: auto;
  margin: auto;
  text-align: center;
  border-color: rgb(238 238 238);
  background: #eeeeee;
  padding: 0 !important;
}

body .container canvas {
  left: 0;
  right: 0;
  margin: auto;
  max-width: 100vw;
  max-height: 100vh;
  height: auto;
  width: auto;
}

div.offcanvas-top {
  height: 90vh;
}

#menu {
  position: fixed;
  top: 0;
  left: 0;
  border: none;
  border-radius: 0;
  margin: 0;
  z-index: 2;
}

#menu:focus,
#menu:active,
#menu:visited,
#menu:hover {
  box-shadow: none;
  background: none;
  color: #6a1a21;
}

#signature {
  font-size: 10px;
  font-family: monospace;
}

@media only screen and (max-width: 40em) {
  div.offcanvas-top {
    height: 100vh;
  }
  #menu {
    padding: 0;
    margin-top: -6px;
  }
}
